export default "<template>\r\n  <vsm-menu :menu=\"menu\">\r\n    <template #default=\"data\">\r\n      <div>{{ data }}</div>\r\n    </template>\r\n    <template slot=\"after-nav\">\r\n      <vsm-mob>Mobile Content</vsm-mob>\r\n    </template>\r\n  </vsm-menu>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data () {\r\n    return {\r\n      menu: [\r\n        { title: 'First item', dropdown: 'first' },\r\n        { title: 'Second item', dropdown: 'second' },\r\n        { title: 'No dropdown' }\r\n      ]\r\n    }\r\n  }\r\n}\r\n</script>\r\n"